import React, {Component} from 'react';
import {Grid, Jumbotron, Navbar} from 'react-bootstrap';
import DynamicForm from './dynamic-form/DynamicForm'

class App extends Component {
    render() {
        return (
            <Grid>
                <Navbar inverse>
                    <Navbar.Header>
                        <Navbar.Brand>
                            Dynamic form
                        </Navbar.Brand>
                        <Navbar.Toggle/>
                    </Navbar.Header>
                </Navbar>

                <Jumbotron>
                    <DynamicForm/>
                </Jumbotron>
            </Grid>
        );
    }
}

export default App;


